* {
    box-sizing: border-box;
}

html, body {
    margin: 0;
    padding: 0;
    height: 100%;
    }

a {
    color: inherit;
    text-decoration: none;
}    

#app {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
}

.header {
    max-width: 70%;
} 

.message-body {
    display: flex;
    padding-top: 1em;
    min-width: 70%;
    justify-content: center;
}

.chatlist {
    border: 1px solid rgba(0, 0, 0, 0.54);
    padding-top: 1em;
    min-width: 30%;
}

form {
    display: flex;
    margin-top: 1em;
    width: 100%;
}

.boxMessage {
   padding-left: 1em;
   min-width: 70%;
}

.MuiFormControl-root{
    width: 100%;
}

.answer-box {
    border: 1px solid  rgba(0, 0, 0, 0.54);
    min-width: 70%;
    min-height: 70vh;
    font-size: 18px;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
}

.message-sender {
    color:#517e7f;
    font-size: 14px;
}

.bot-message {
    background-color: lightgrey;
    align-self: flex-start;
    padding: 5px 15px;
    border-radius: 20px;
    margin: 5px;
    
}

.writer-message {
    background-color: #abd6f1;
    align-self: flex-end;
    padding: 5px 15px;
    border-radius: 20px;
    margin: 5px;
}


